<template>
    <section>

        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.SubjectName" placeholder="专业名称" @input="getSubjects"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="filters.SubjectAdmin" placeholder="所属学院" @input="getSubjects"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getSubjects">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd">新增专业</el-button>
                </el-form-item>
            </el-form>
        </el-col>



        <template>
            <el-col :span="24" style="padding-bottom: 0px; padding-top: 10px">
                <el-card>
                    <el-table
                            :data="subjects"
                            stripe
                            style="width: 100%">
                        <el-table-column
                                prop="id"
                                v-if="false"
                        >
                        </el-table-column>
                        <el-table-column
                                prop="subjectName"
                                label="专业名称"
                        >
                        </el-table-column>
                        <el-table-column
                                prop="subjectAdmin"
                                label="所属学院"
                        >
                        </el-table-column>
                        <el-table-column label="操作" min-width="100">
                            <template scope="scope">
                                <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                                <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-card>
            </el-col>
            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size='pageSize' :total="total" style="float:right;">
                </el-pagination>
            </el-col>
        </template>

        <!--新增界面-->
        <el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item prop="id" v-if="false">
                    <el-input v-model="addForm.id" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="专业名称" prop="SubjectName">
                    <el-input v-model="addForm.SubjectName" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="所属学院" prop="SubjectAdmin">
                    <el-input v-model="addForm.SubjectAdmin" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>

        <!--编辑界面-->
        <el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item prop="id" v-if="false">
                    <el-input v-model="editForm.id" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="专业名称" prop="subjectName">
                    <el-input v-model="editForm.subjectName" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="所属学院" prop="subjectAdmin">
                    <el-input v-model="editForm.subjectAdmin" auto-complete="off"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">修改</el-button>
            </div>
        </el-dialog>


    </section>


</template>

<script>
    import {addAuth, addSubject, editSubject, getSubjectList, removeSubject } from '../../api/api'
    export default {
        // name: "NoticeList"
        data() {
            return {
                filters: {
                    SubjectName: '',
                    SubjectAdmin:'',
                },
                subjects: [],
                pageSize: 20,
                total: 0,
                addForm: {
                    id: '',
                    SubjectName: '',
                    SubjectAdmin:'',
                },
                editForm: {
                    id: '',
                    subjectName: '',
                    subjectAdmin:'',
                },
                editLoading: false,
                editFormVisible: false,
                editFormRules: {
                    subjectName:  [
                        { required: true, message: '请输入专业名称', trigger: 'blur' }
                    ],
                    subjectAdmin:  [
                        { required: true, message: '请输入所属学院', trigger: 'blur' }
                    ],
                },
                addLoading: false,
                addFormVisible: false,

                addFormRules: {
                    SubjectName:  [
                        { required: true, message: '请输入专业名称', trigger: 'blur' }
                    ],
                    SubjectAdmin:  [
                        { required: true, message: '请输入所属学院', trigger: 'blur' }
                    ],
                },
                utype: '',
                sid: 0,
            }
        },
        methods: {
            handleCurrentChange(val) {
                this.page = val;
                this.getSubjects();
            },
            getSubjects() {
                let para = {
                    SubjectName: this.filters.SubjectName,
                    SubjectAdmin: this.filters.SubjectAdmin,
                    page: this.page,
                    pageSize: this.pageSize,
                }
                getSubjectList(para).then(res=>{
                    console.log(res)
                    this.subjects = res.data.subjects
                    this.total = res.data.total
                })
            },
            //显示新增界面
            handleAdd: function () {
                // 查询用户是否存在简历

                this.addFormVisible = true;
                this.addForm = {
                    id: '',
                    SubjectName: '',
                    SubjectAdmin: '',
                };
            },
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    // if (valid) {
                    this.$confirm('确认提交吗？', '提示', {}).then(() => {
                        this.addLoading = true;
                        //NProgress.start();
                        // let para = Object.assign({}, this.addForm);
                        let pars = {
                            subjectName: this.addForm.SubjectName,
                            subjectAdmin: this.addForm.SubjectAdmin,
                        }
                        // console.log(pars)
                        addSubject(pars).then((res) => {
                            this.addLoading = false;
                            //NProgress.done();
                            this.$message({
                                message: '提交成功',
                                type: 'success'
                            });
                            this.$refs['addForm'].resetFields();
                            this.addFormVisible = false;
                            this.getSubjects();
                        });
                    });
                    // } else {
                    // 	console.log('error submit!')
                    // }
                });
            },
            //显示编辑界面
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //编辑
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗？', '提示', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = {
                                id: this.editForm.id,
                                subjectName: this.editForm.subjectName,
                                subjectAdmin: this.editForm.subjectAdmin,
                            }
                            editSubject(para).then((res) => {
                                this.editLoading = false;
                                //NProgress.done();
                                this.$message({
                                    message: '提交成功',
                                    type: 'success'
                                });
                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getSubjects();
                            });
                        });
                    }
                });
            },
            //删除
            handleDel: function (index, row) {
                this.$confirm('确认删除该记录吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let para = { id: row.id };
                    // console.log(para)
                    removeSubject(para).then((res) => {
                        this.listLoading = false;
                        //NProgress.done();
                        this.$message({
                            message: '删除成功',
                            type: 'success'
                        });
                        this.getSubjects();
                    });
                }).catch(() => {

                });
            },
        },
        mounted() {
            var user = sessionStorage.getItem('user')
            var userJSON = JSON.parse(user)
            console.log(user)
            this.utype = userJSON.utype
            this.sid = userJSON.fkno
            this.getSubjects()
        }
    }
</script>

<style scoped>
    el-card {
        padding-top: 20px;
    }
</style>


